
* {
    font-family: Helvetica, Arial, sans-serif;
    margin: 0;
}

html, body {
    height: 100vh;
    width: 100vw;
}

::-webkit-scrollbar {
    width: 12px;
}
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}

main {
    width: 100%;
}

.mt-1{
    margin-top:  5px;
}

.mt-5{
    margin-top:  20px;
}

.mb-1{
    margin-bottom:  5px;
}
.mb-5{
    margin-bottom:  20px;
}

.aling-center{
    text-align: center;
}

.pointer{
    cursor: pointer;
}